* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  margin: 40px;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.flag-img {
  display: inline-block;
  width: 50px;
  padding-right: 10px;
}
.country-name {
  font-size: 30px;
  display: inline-block;
  font-weight: 600;
  margin: 0;
}
.country-content {
  padding-top: 10px;
}
.country-content__item {
  margin-bottom: 5px;
}
.country-content__info {
  font-weight: 600;
  font-size: 20px;
}

.country-list-name {
  font-size: 20px;
  display: inline-block;
  font-weight: 400;
  margin: 0;
  margin-top: 10px;
}
.flag-list-img {
  display: inline-block;
  width: 40px;
  padding-right: 10px;
}
#search-box {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 3px solid grey;
  transition: 0.5s;
  outline: none;
  margin-bottom: 15px;
}
#search-box:focus {
  border: 3px solid slateblue;
}
.search-headline {
  font-size: 20px;
  margin: 0;
  color: grey;
}
